body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: black;
    position: relative;
    z-index: 1;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
@font-face {
    font-family: "Commuter Sans";
    src: url(/Tusmo/static/media/commuter-sans-light.e12e32ed.otf) format("opentype");
}

body {
    font-family: "Commuter Sans";
}

.display-none {
    display: none;
}
.case {
    border: 1px solid white;
    width: 80px;
    height: 80px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    margin: 0;
    position: relative;
}

.well-placed {
    background-color: red;
}

.misplaced::after {
    content: "";
    color: orange;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: orange;
    position: absolute;
    z-index: -1;
}

.row {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row;
    position: relative;
}

.nav {
    font-size: 60px;
    padding: 10px;
    color: white;
    border-bottom: 2px solid white;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.panel {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    color: white;
}

.error{
	color: white;
    text-align: center;
    transition: all 1s;
}

.hide{
    opacity: 0;
}
.buttons {
	overflow: hidden;
}

.buttons a {
	float: left; 
	margin-right: 20px;
  margin-bottom: 20px;
}
a#link {position: absolute; left: 20px; bottom: 10px; text-decoration: none; color: #555; font-weight: bold;}
/*------------------------------------------------------*/
/* BUTTONS */
/*------------------------------------------------------*/
.btn,
input[type="submit"]{
	cursor: pointer;
    border-radius: 0px;
    text-decoration: none;
    padding: 12px 18px;
    font-size: 12px;
    line-height: 19px;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif; font-weight:400;
    letter-spacing: 3px;
    transition: all .4s ease-in-out;
}

.btn-big {
    width: 30%;
    margin-top: 50px;
    text-decoration: none;
    font-size: 200%;
    line-height: 40px;
    text-transform: uppercase;
    text-align: center;
    font-family: 'Montserrat', sans-serif; font-weight:400;
    letter-spacing: 3px;
    transition: all .4s ease-in-out;
}
.btn:hover,
input[type="submit"]:hover{
    transition: all .4s ease-in-out;
}
.btn-white{
    border:solid 2px #fff;
    background: transparent;
    color: #fff !important;
}
.btn-white:hover{
    border:solid 2px #fff;
    background: #fff;
    color: #1f1f1f !important;
}

.btn-color{
    background: transparent;
}
.btn-color:hover{
    color: #fff;
}

.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
.stat {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
  }

h1 {
    color: white;
}

.tries{
  text-align: center;
}

.try{
  margin: 0;
}

.end-game-state{
  font-size: 60px;
}

.number{
  font-family: sans-serif;
  font-size: 25px;
}
.keyboard {
    margin: 10px;
}

.key-row div {
    border: 1px solid white;
    width: 35px;
    height: 40px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    margin: 1px;
    position: relative;
    border-radius: 5px;
}

.key-row {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row;
    position: relative;
}

.missing {
    opacity: 0.5;
}

.misplaced-keyboard {
    background-color: orange;
}

button {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}
